<template>
  <div class="index">
    <div class="container">
      <div class="main">
      <input class="search" type="text" v-model="search" placeholder="搜索内容">
      <div class="show-blog" v-for="(item , index) in filterblog" :key="index">
        <router-link :to="'/detail/' + item.id">
          <div class="content">
              <h3>{{item.title}}</h3>
              <article>{{item.content | singler}}</article>
        </div>
        </router-link>
      </div>
    </div>
    <div class="side">
      <table>
        <tr>
          <td>作者：huaker</td>
        </tr>
        <tr>
          <td>座右铭：纵横捭阖，冷心为上。</td>
        </tr>
        <tr>
          <td>介绍：喜欢专研不懂的问题</td>
        </tr>
      </table>
    </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'index',
  data() {
    return{
      blog:[],
      search:""
    }
  },
  created(){
    axios.get('https://blog-3b81b.firebaseio.com/posts.json/')
    .then((data) =>{
      var obj = data.data
      var arry = []
      //将对象转化为数组
      for (let i in obj){
        obj[i].id = i
        arry.push(obj.[i])
      }
      this.blog = arry
    })
  },
  computed:{
    //过滤搜索
    filterblog(){
      return this.blog.filter((blog) =>{
        return blog.title.match(this.search)
      })
    }

  }

}
</script>

