<template>
    <div class="detail">
        <h2>{{blog.title}}</h2>
        <article>{{blog.content}}</article>
        <hr>
        <div>作者：{{blog.auther}}</div>
        <label for="">分类：
            <ul v-for="(item , index) in blog.categories" :key="index">
            <li>{{item}}</li>
        </ul>
        </label>
        <div><router-link class="edit" :to="'/edit/' + id">编辑</router-link><button @click="deletes()">删除</button></div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    name:"detail",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    methods:{
        deletes(){
            axios.delete('https://blog-3b81b.firebaseio.com/posts/' + this.id + '.json')
            .then(data =>{
                this.$router.push({path:'/'})//删除成功跳转
            })
        }
    },
    created(){
        axios.get('https://blog-3b81b.firebaseio.com/posts/' + this.id + ".json")
        .then((data) =>{
            //console.log(data)
            this.blog = data.data
        })
    }
}
</script>

<style scoped>
.detail{
    max-width: 960px;
    margin:10px auto;
    padding: 10px;
    border: 1px dotted #f5f6fa;
    border-radius: 5px;
    background: #dcdde1;
}
article{
    min-height: 150px;
}
.detail article,div,label,ul{
    margin:5px;
    padding: 5px;
}
.detail div{

}
ul{
    list-style: none;
}
.detail div:nth-child(2){
    box-sizing: border-box;
    display: block;
}
button,.edit{
    text-decoration: none;
    padding: 5px;
    margin-left: 10px;
    border: none;
    font-size: 20px;
    color: black;
    border-radius: 5px;
    background-color: white;
}
</style>