<template>
  <div class="add-blog">
    <form action="" v-if="!sumbit">
        <h2>编辑</h2>
      <label for="">博客标题</label>
      <input type="text" v-model="blog.title">
      <label for="">博客内容</label>
      <textarea v-model="blog.content"></textarea>
      <div id="checkboxs">
        <label for="">VUE.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label for="">android</label>
        <input type="checkbox" value="android"  v-model="blog.categories">
        <label for="">java</label>
        <input type="checkbox" value="java"  v-model="blog.categories">
        <label for="">其他</label>
        <input type="checkbox" value="其他"  v-model="blog.categories">
      </div>
      <label for="">作者：</label>
      <select name="" id="" v-model="blog.auther">
        <option v-for="(auther , index) in auther" :key="index">{{auther}}</option>
      </select>
      <button @click.prevent="post()">提交</button>
    </form>
    <div v-if="sumbit">
      <div id="preview">
          <h3>编辑成功</h3>
          <label for="">博客标题</label>
            <input type="text" v-model="blog.title">
            <label for="">博客内容</label>
            <textarea v-model="blog.content"></textarea>
            <div id="checkboxs">
                <label for="">VUE.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label for="">android</label>
                <input type="checkbox" value="android"  v-model="blog.categories">
                <label for="">java</label>
                <input type="checkbox" value="java"  v-model="blog.categories">
                <label for="">其他</label>
                <input type="checkbox" value="其他"  v-model="blog.categories">
            </div>
            <label for="">作者：</label>
            <select name="" id="" v-model="blog.auther">
                <option v-for="(auther , index) in auther" :key="index">{{auther}}</option>
            </select>
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'add-blog',
  data(){
    return{
        id:this.$route.params.id,  
        blog:{},
        auther:['huaker','Tom'],
        sumbit:false
    }
  },
  methods:{
    post(){
      axios.put('https://blog-3b81b.firebaseio.com/posts/' + this.id + '.json',
      this.blog
      ).then(data =>{
      }),
      this.sumbit = true
    }
  },
  created(){
        axios.get('https://blog-3b81b.firebaseio.com/posts/' + this.id + '.json')
        .then(data=>{
            this.blog = data.data
        })
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.add-blog *{
  box-sizing: border-box;
}
.add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  border-radius: 5px;
  background-color: #dcdde1;
}
form label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 10px;
}
textarea{
  height: 200px;
}
#checkboxs label{
  display: inline-block;
  margin-top: 5px;
}
#checkboxs input{
  display: inline-block;;
  margin-right: 10px;
}
button{
  border: 0;
  margin:20px 0;
  padding: 10px;
  background-color: white;
  border-radius: 2px;
  font-size: 20px;
}
</style>
